*{
    margin:0;
    padding: 0;
}
body{
    background-color: #333;
  }

.main-container{
    display:flex;
    justify-content: center;
    align-items: center;
    height: 100vh;
}
.center-container{
    height: 500px;
    width:350px;
}
.app-heading{
    padding: 4px;
    color: white;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    text-transform: uppercase;
}

.input-container{
    display: flex;
    justify-content: center;
    margin-bottom: 10px;
}
.input-box-todo{
    margin-left: 10px;
    width:70%;
    height:40px;
    border-radius: 14px;
    border:1px solid black;
    padding-left: 10px;
    box-shadow: 0px 10px 20px rgba(0,0 ,0,0.3);
    outline: none;
    font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
    font-size: large;
}
.add-btn{
    background-color: #316fc1;
    margin-left: 10px;
    width: 40px;
    height: 40px;
    color: white;
    border-radius: 50%;
    border: none;
    font-weight: bolder;
  font-size: larger;
  cursor: pointer;
  box-shadow: 0px 5px 10px rgba(11, 10, 10, 0.3);
  transition: 0.3s;
}
.add-btn:active{
    box-shadow: none;
}
.list-item{
    list-style-type: none;
    border:2px solid rgb(161,158,158);
    color: white;
    padding: 3px;
    padding-left: 5px;
    border-radius: 5px;
    width: 100%;
    height: 30px;
    display: flex;
  align-items: center;
  position: relative;
  margin-top: 10px;
}
.icons{
    position: absolute;
    right: 10px;
  }

  .icons2{
    margin:4px;
    font-size: large;
    cursor: pointer;
    transition: 0.1s;
  }
  .icons2:hover{
    transform: scale(1.2);  
  }
  .icon-delete:hover{
    color: red;
  }